<script>
  import moment from 'moment'
  import {mapGetters} from 'vuex'
  import { Bus } from '../../EventBus'
  import BetForm from "../Utilities/BetForm";
  import LandingButtons from "../Utilities/LandingButtons";
  const Loading = () => import('../Utilities/LoadingIndicator');

  export default {
    props: ['games', 'isGroup'],
    components: {LandingButtons, BetForm, Loading },
    data () {
      return {
          inBasketball: false,
          gamesInBetSlip: [],
          inMoreMarkets: false,
          over_under_handicap: 2.5,
          upcoming: false,
          isHighlight: true,
          isFeatured: false,
          noHighlightedGames: false,
          noFeaturedGames: false,
          daySelected: 'today'
      }
    },
    mounted () {
      this.$EventBus.$on('in-basketball', (payload) => this.inBasketball = payload.inBasketball);
      this.$EventBus.$on('gameComponentChanged', (component) => {
        this.switchComponents(component)
      });
    },

    computed: {
      ...mapGetters(['betSlip'])
    },
    watch: {
      'betSlip' () {
        this.$forceUpdate()
      }
    },
    methods: {
      switchComponents (component) {
          if (component === 'highlihgts') {
            this.isHighlight = true;
            this.upcoming = false;
            this.isFeatured = false;
          } else if (component === 'featured') {
            this.isFeatured = true;
            this.upcoming = false;
            this.isHighlight = false;
          } else if (component === 'upcoming') {
            this.upcoming = true;
            this.isFeatured = false;
            this.isHighlight = false;
          } else {
            this.isHighlight = true;
            this.upcoming = false;
            this.isFeatured = false;
          }
      },
      loadStatistics(id) {
        let eventId = null;
        if (id)
        eventId = id;
        else eventId = 6117;
        window.open(
          'http://score24.com/statistics3/head2head.jsp?partner=rafikibet&gameId='+eventId+'&lang=eng','popUpWindow',
          'height=1000,width=1000,left=100,top=100,resizable=yes,scrollbars=yes,toolbar=yes,menubar=no,location=no,directories=no,status=yes')
      },

      removeFromBetSlip (game) {
        Bus.$emit('gameRemoved');
        this.$store.dispatch('removeFromSlip', game)
      },
      addToBetSlip (game, odd, pick) {
        Bus.$emit('gameAdded');
        this.$store.dispatch('updateBetSlip', {game: game, odd: odd});
        this.isSelected(game, pick)
      },

      hasOdds (game) {
        let isOdds = false;
        game.forEach((odds) => {
          if (odds.value >= 1.01) {
            isOdds = true
          }
        });
        return isOdds
      },
      isSelected (game, pick) {
          let isSelected = false;
        if (this.betSlip) {
          this.betSlip.filter(item => {
            if (item.gameId === game.game_id && item.oddId === pick) {
              isSelected = true
            }
          });
          return isSelected
        }
      },
    gameIsSelected (gameId) {
        let isSelected = false;
        if (this.betSlip) {
            this.betSlip.filter(item => {
                if (item.gameId === gameId && item.odd.market_name !== 'Match Result') {
                    isSelected = true
                }
            });
            return isSelected
        }
    },
      loadMoreMarkets (game) {
        // Set the more market game to local storage
        this.$store.dispatch('getGameMarkets', game);
        this.$router.push({name: 'more-markets'})
      },
      fullTime (scheduled) {
        return moment(scheduled).format('HH:mm')
      },
      dateName (scheduled) {
        return moment.utc(scheduled).local().format('ddd, MMM Do')
      },
      displayTime (scheduled) {
        return moment.utc(scheduled).local().format('HH:mm')
      },
      mobileDate (scheduled) {
        return moment.utc(scheduled).local().format('ddd')
      }
    }
  }
</script>

